import { AiFillCaretUp } from "react-icons/ai";

import "./Description.css";

export const Baner = ({ open, setOpen, ...props }) => {
  const baner = {
    backgroundImage: `url(https://s3-alpha-sig.figma.com/img/70d7/979d/f999ac53acf59c27fcd3ac42cadd69f2?Expires=1679875200&Signature=Rnn6uxZHRzEdV~EsYhYyqhlqcQbe9S-PQcRJe1x8gpDTttBt9laceSsvzUc-4Oh1J73DSfuBGUDANZs3vF-q2DQedltYJJYtdwXun8jpmqzuY4dNTWEhoBVFOlvvUO0RGLthnFc-uws5O-Tci04sm1nkECxVjMGQvAUiww2sX-Eho3Lre8k4MKqCPQ9rmC2~L2IeZxSzcs~ncWbviUZDt1SRP9cWh0oKCDHESzBMdWmlpzi6MajkEe1ovQ6UkOKwe4XAldS6UdyQ3zLyLXoNdaSPxq9XsOCNbv6VP~EB8Kf-45RjC9FYpZMDO78gJW4Y1cC0jFrFvPW8MynzG5voTA__&Key-Pair-Id=APKAQ4GOSFWCVNEHN3O4)`,
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    backgroundPosition: "center center",
    padding: "105px 26.5% 101px 26.5%",
    position: "relative"
  };
  const title = {
    fontSize: "50px",
    lineHeight: "55px",
    marginBottom: "30px",
    color: "white"
  };
  const subTitle = {
    fontSize: "20px",
    color: "white",
    marginTop: "50px"
  };
  const descriptionBlock = {
    backgroundColor: "#fefec9"
  };
  const p = {
    // fontFamily: "Vollkorn-Regular",
    fontSize: "18px",
    lineHeight: "25px",
    color: "#47474f",
    padding: "60px 17.5% 59px 17.5%"
  };
  // const caretUp = {
  //   marginTop: "-50px",
  //   backgroundColor: "#fefec9"
  // };
  return (
    <>
      <div style={baner}>
        <h1>
          <a style={title} href="#0">
            {props.baner.title}
          </a>
        </h1>
        <p style={subTitle}>{props.baner.subTitle}</p>
      </div>
      <div style={descriptionBlock}>
        <button onClick={() => setOpen(false)} className="X">
          <div className="img">
            <AiFillCaretUp size={60} />
          </div>

          {/* <img className="img" alt="buttonX" /> src={buttonX} */}
        </button>
        <div>
          <div className={`description ${open ? "open" : ""}`}>
            <p style={p}>{props.baner.description}</p>
          </div>
          <button className="buttonOpen" onClick={() => setOpen(true)}>
            <img
              className="imgDown"
              src="https://free-png.ru/wp-content/uploads/2022/01/free-png.ru-206.png"
              alt=""
            />
          </button>
        </div>
      </div>
    </>
  );
};
