import { useState, useEffect, useCallback, useMemo } from "react";

const countTotal = (num) => {
  console.log("counting...");
  return num + 10;
};

export function Test() {
  const [count, setCount] = useState(0);

  useEffect(() => {
    document.title = `Счетчик: ${count}`;
  }, [count]);

  const someImages = useCallback(() => {
    console.log("fetchig");
    return [
      "https://avatars.mds.yandex.net/i?id=fc16fc98f7def71dafd21bd393583619f18765da-8312144-images-thumbs&n=13",
      "https://avatars.mds.yandex.net/i?id=2bd4929aba4c9fd461bc0f19ebbadd40676b0b0d-7544473-images-thumbs&n=13"
    ];
  }, [count]);

  function countPlus() {
    setCount(count + 1);
  }
  function countMinus() {
    setCount(count - 1);
  }
  function random() {
    setCount(Math.round(Math.random() * 100));
  }

  function reset() {
    setCount(0);
  }

  const total = useMemo(() => {
    return countTotal(count);
  }, [count]);

  return (
    <>
      {/* {someImages().map((url, i) => {
        return <img key={i} src={url} alt="alt" />;
      })} */}
      <Image someImages={someImages} />
      {count}
      Total count: {count}
      <button onClick={() => countPlus()}>+</button>
      <button onClick={() => countMinus()}>-</button>
      <button onClick={() => random()}>random</button>
      <button onClick={() => reset()}>reset</button>
    </>
  );
}

const Image = ({ someImages }) => {
  const [images, setImages] = useState([]);

  useEffect(() => {
    setImages(someImages());
  }, [someImages]);
  return (
    <>
      {images.map((url, i) => {
        return <img key={i} src={url} alt="alt" />;
      })}
    </>
  );
};
